import dateFormat from './date'

function toText (val, before, after) {
  return Math.abs(val) + (val > 0 ? before : after)
}

function fromNow (date, scope, format) {
  let ms = Date.now() - date.getTime()

  // 
  if (ms > 0) ms += 1000
  else ms -= 1000

  const minute = parseInt(ms / 1000 / 60)
  const hour = parseInt(minute / 60)
  const day = parseInt(hour / 24)
  const month = parseInt(day / 30)
  const year = parseInt(day / 365)

  if (year) {
    if (scope >= 6) return toText(year, '', '')
  } else if (month) {
    if (scope >= 5) return toText(month, '', '')
  } else if (day) {
    if (scope >= 4) return toText(day, '', '')
  } else if (hour) {
    if (scope >= 3) return toText(hour, '', '')
  } else if (minute) {
    if (scope >= 2) return toText(minute, '', '')
  } else if (scope >= 1) return ms > 0 ? '' : ''

  // if (year && scope >= 6) return toText(year, '', '')
  // if (month && scope >= 5) return toText(month, '', '')
  // if (day && scope >= 4) return toText(day, '', '')
  // if (hour && scope >= 3) return toText(hour, '', '')
  // if (minute && scope >= 2) return toText(minute, '', '')
  // if (scope >= 1) return ms > 0 ? '' : ''

  return dateFormat(date, format)
}

const scopeMap = {
  'moment': 1,
  'second': 1,
  'minute': 2,
  'hour': 3,
  'day': 4,
  'month': 5,
  'year': 6
}

export default function (date, scope = 'year', format) {
  if (typeof date === 'number' || typeof date === 'string' || date instanceof Date) date = new Date(date)
  else return ''

  if (typeof scope === 'string') scope = scopeMap[scope]

  return fromNow(date, scope, format)
}



